<template>
  <div>
    <ul>
      <li v-for="num in numbers" :key="num">{{ num }}</li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Numbers",
  data() {
    return {
      numbers: [],
    };
  },
  async mounted() {
    let ns = await axios.get(`${this.$store.state.server}/payments/numbers`);
    if (ns.status === 200) {
      this.numbers = ns.data;
    } else {
      alert(ns.data);
    }
  },
};
</script>

